@import 'assets/settings';

/*header*/

@each $socialLink in $socialLinks {
    .socials-block__list-item.#{$socialLink} {
        .socials-block__list-link {
            background-color: color('#{$socialLink}');
        }    
    }
}

a {
	text-decoration: none;
}

.site-header {
	background-color: #fff;
		.container-wrapper {
			background-image: url(../img/main/header-bgi.jpg);
			background-repeat: no-repeat;
			background-position-x: center;
		}
		.container-inner {
			@extend %clearfix;
		}
}



.socials-block {
	&__list {

		&-item {
			position: relative;
			display: inline-block;
			width: 24px;
			height: 24px;
			margin-right: 10px;
		}
		&-link {
			display: inline-block;
			width: 100%;
			height: 100%;
			-webkit-border-radius: 50%;
			   -moz-border-radius: 50%;
			        border-radius: 50%;
			.fa {
				position: absolute;
				color: #fff;
				font-size: 12px;
				top: 50%;
				left: 50%;
				-webkit-transform: translate(-50%, -50%);
				   -moz-transform: translate(-50%, -50%);
				    -ms-transform: translate(-50%, -50%);
				     -o-transform: translate(-50%, -50%);
				        transform: translate(-50%, -50%);
			}
		}
	}
}

.header-contacts {
		width: 30%;
		float: left;
		text-align: right;
		margin-right: 20px;
	&__phones {
		display: inline-block;
		background-image: url(../img/main/phone.png);
		background-repeat: no-repeat;
		background-position: 0px 13px;
		padding: 5px 20px;
		

		&-link {
			display: block;
			font-family: Verdana;
			font-size: 13px;
			line-height: 16px;
			color: #333333;
		}	
	}
}

.header-main {
	float: left;
	.header-nav {
		&__list {
			&-item {
				display: inline-block;
			}
		}
	}
}

/*END:header*/